
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";
import { blocklyNew } from "./blockly/Blockly";
import {
  filename,
  save,
  userData,
  mode,
  xml,
  isUser,
  open,
  isSaved,
  currentFileRef,
  share
} from "../scripts/state/useState";
import { openModal } from "@/scripts/state/useModalState";
import UserAvatar from "@/components/UserAvatar.vue";
import { listFirebaseFiles } from "@/scripts/state/useFirebase";
import firebase from "firebase";
import { useToast } from "vue-toastification";
import { useI18n } from "vue-i18n";

export default {
  name: "Nav",
  components: {
    FontAwesomeIcon,
    UserAvatar
  },
  setup() {
    const toast = useToast();
    const { t } = useI18n();

    const openFilesModal: Function = () => {
      listFirebaseFiles();
      openModal("FilesModal");
    };

    const shareModalOpen: Function = () => {
      if (isSaved.value) {
        share();
      } else {
        toast.error(t("ShareMessage"));
      }
    };

    const saveFirebaseFile: Function = () => {
      const user: firebase.User | null = firebase.auth().currentUser;
      if (user) {
        const platform = " (" + mode.value + ")";
        const ref = firebase
          .storage()
          .ref(`blocks/${user.uid}/${filename.value}${platform}`);
        const task = ref.putString(xml.value, undefined, {
          contentType: "text/xml"
        });
        task.on(
          "state_changed",
          function(snapshot) {
            //Saving
            snapshot;
          },
          function(error) {
            toast.error(error.message);
          },
          function() {
            currentFileRef.value = ref;
            toast.success(filename.value + " " + t("Successfully Saved") + "!");
            isSaved.value = true;
          }
        );
      } else {
        save();
      }
    };

    return {
      filename,
      blocklyNew,
      openModal,
      userData,
      save,
      mode,
      open,
      shareModalOpen,
      listFirebaseFiles,
      openFilesModal,
      saveFirebaseFile,
      currentFileRef,
      isUser
    };
  }
};
