
import {
  listFirebaseFiles,
  openFirebaseFile,
  deleteFile
} from "@/scripts/state/useFirebase";
import { files, open, fileListKey } from "@/scripts/state/useState";
import firebase from "firebase";
import { ref, Ref } from "vue";
import { closeModal } from "@/scripts/state/useModalState";
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";

export default {
  name: "FilesModal",
  components: {
    FontAwesomeIcon
  },
  setup() {
    const user: Ref = ref(firebase.auth().currentUser);

    const greet: Function = () => {
      const myDate = new Date();
      let greet;
      const hrs = myDate.getHours();
      if (hrs < 12) greet = "Morning";
      else if (hrs >= 12 && hrs <= 17) greet = "Afternoon";
      else if (hrs >= 17 && hrs <= 24) greet = "Evening";
      return greet;
    };

    return {
      listFirebaseFiles,
      files,
      openFirebaseFile,
      user,
      greet,
      closeModal,
      open,
      fileListKey,
      deleteFile
    };
  }
};
